/* src/app/pages/panier/panier.scss */
main {
  background-color: #fff8e1;
  font-family: "Roboto", sans-serif;
}
.hero {
  height: 280px;
  display: flex;
  align-items: center;
  position: relative;
  background-color: rgba(245, 241, 224, 0.4941176471);
  padding-top: 8rem;
  padding-bottom: 8rem;
}
.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.hero h1,
.hero p {
  position: relative;
  z-index: 2;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}
.card {
  border: none;
  transition: transform 0.3s ease;
}
.card:hover {
  transform: translateY(-5px);
}
.table {
  background-color: #fff;
}
.table th,
.table td {
  vertical-align: middle;
}
.form-control {
  border-color: #d97706;
  background-color: #fff8e1;
}
.form-control:focus {
  border-color: #14532d;
  box-shadow: 0 0 0 0.2rem rgba(20, 83, 45, 0.25);
}
.text-success {
  color: #15803d !important;
}
.text-success:hover {
  color: #1ec962 !important;
}
.text-amber {
  color: #d97706 !important;
}
.btn-warning {
  background-color: #d97706;
  border-color: #d97706;
  padding: 0.75rem 2rem;
}
.btn-warning:hover,
.hover-dark-green:hover {
  background-color: #14532d;
  border-color: #14532d;
}
.btn-outline-success {
  border-color: #15803d;
  color: #15803d;
}
.btn-outline-success:hover {
  background-color: #14532d;
  color: #fff;
}
.bg-light-amber {
  background-color: #fff8e1;
}
.bg-gradient-amber {
  background:
    linear-gradient(
      180deg,
      #fff8e1 0%,
      #f5e0b5 100%);
}
.text-dark {
  color: #333 !important;
}
.container-md {
  max-width: 960px;
}
.py-6 {
  padding-top: 4.5rem;
  padding-bottom: 4.5rem;
}
/*# sourceMappingURL=panier.css.map */
